@import url('https://fonts.googleapis.com/css2?family=Hepta+Slab:wght@1..900&display=swap');

.project {
  position: relative;
}

.project-cover img{
  max-width: 100%; 
  height: auto; 
  display: block;
  transition: opacity 0.7s ease;
}

.project-info{
  position: absolute;
  top:0;
  left:0;
  right:0;
  bottom:0;
  background: #065517;
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  gap: 1rem;
  opacity: 0;
  transition: opacity 0.7s ease;
  pointer-events: none;
}

.title {
  font-size: 5vw;
  padding: 0.5vw;
}

.hepta-slab-white {
  font-family: "Hepta Slab", serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  color: aliceblue;
}

.hepta-slab-black {
  font-family: "Hepta Slab", serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  color: black;
}

.project:hover .project-info {
  pointer-events: auto;
}

.project:hover .project-cover img {
  opacity: 0;
}

.project:hover .project-info {
  opacity: 1;
}

.icon {
  image-rendering: pixelated;
  height: 5vw;
  width: 5vw;
  padding: 0.5vw;
}

.header-with-logo {
  display: flex;
  align-items: center; 
  gap: 1rem;
  background-color: white;
}

.cloud-header {
  width: 100vw;
  image-rendering: pixelated;
  padding: 0;
}

.website-header {
  position: fixed;
  z-index: 9999;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  /*background-color: rgb(2, 22, 40);*/
  background-image: url('./images/grassTile.png');
  background-size: 50vw;
  image-rendering: pixelated;
  min-height: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  padding: 0;
}

.App-link {
  color: #61dafb;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
